<template>
	<div class="MyWallet">
		<div class="home-header">
			<a style="display: table-cell;
    vertical-align: middle;text-align: center;">
				<img :src="walletInfo.avatar || defaultAvatar" width="80" height="80" style="border-radius: 50%;"/>
				<p v-if="walletInfo.walletName" style="color: #CEBC98;margin: 0;line-height: 40px;">{{walletInfo.walletName}} <i class="iconfont" style="display:inline-block;
  vertical-align:middle;  ">&#xe6a9;</i></p>
			</a>
		</div>
		<div style="margin-top: 10px;background: white;">
		  <mu-list>
		  	<!--to="/editPassword"-->
		    <!--<mu-list-item button @click="showTips('该功能暂未开放！')">
		      <mu-list-item-title>修改密码</mu-list-item-title>
		      <mu-list-item-action>
		        <i class="icon iconfont">&#xe695;</i>
		      </mu-list-item-action>
		    </mu-list-item>-->
		    <mu-list-item button @click="editPassword">
		      <mu-list-item-title>修改密码</mu-list-item-title>
		      <mu-list-item-action>
		        <i class="icon iconfont">&#xe695;</i>
		      </mu-list-item-action>
		    </mu-list-item>
		    <mu-list-item v-if="walletInfo.secretSeed" button @click="walletBackups">
		      <mu-list-item-title>钱包备份</mu-list-item-title>
		      <mu-list-item-action>
		        <i class="icon iconfont">&#xe695;</i>
		      </mu-list-item-action>
		    </mu-list-item>
		    <!--to="/exportPrivateKey"-->
		    <mu-list-item button @click="exportKey">
		      <mu-list-item-title>导出私钥</mu-list-item-title>
		      <mu-list-item-action>
		        <i class="icon iconfont">&#xe695;</i>
		      </mu-list-item-action>
		    </mu-list-item>
		    <!--to="/deleteWallet"-->
		    <mu-list-item button @click="deleteWallet">
		      <mu-list-item-title>删除钱包</mu-list-item-title>
		      <mu-list-item-action>
		        <i class="icon iconfont">&#xe695;</i>
		      </mu-list-item-action>
		    </mu-list-item>
		  </mu-list>
		</div>
		<transition name="router-slid"  mode="out-in">
    		<router-view  class="child-view"></router-view>
    	</transition>
	</div>
</template>

<script>
export default {
  name: 'HomeDetails',
  data(){
  	return{
  		defaultAvatar:require('../../assets/img/logo.png'),
  		walletInfo:{}
  	}
  },
  methods:{
  	//修改密码
  	editPassword(){
  		this.$router.push({path:'/editPassword',query:{details:this.$route.query.details}})
  	},
  	//导出私钥
  	exportKey(){
  		this.$router.push({path:'/exportKey',query:{details:this.$route.query.details}})
  	},
  	//钱包备份
  	walletBackups(){
  		this.$router.push({path:'/walletBackups',query:{details:this.$route.query.details}})
  	},
  	//删除钱包
  	deleteWallet(){
  		this.$router.push({path:'/deleteWallet',query:{details:this.$route.query.details}})
  	},
  	//忘记密码
  	forgetPassword(){
  		//:to="'/forgetPassword/'+walletInfo.id"
  		if(this.walletInfo.id){
  			this.$router.push('/forgetPassword/'+this.walletInfo.id);
  		}
  	},
  	showTips(tip){
  		let _this = this;
  		_this.$layer.open({
		    content: tip
		    ,skin: 'msg'
		    ,time: 2
	  	});
  	},
  },
  computed:{
  	
  },
  mounted(){
	this.walletInfo = JSON.parse(this.$route.query.details);
  },
  watch:{
  	$route (to,from){
  		if(to.name == '我的钱包' && from.path == '/walletManage'){
  			this.walletInfo = JSON.parse(this.$route.query.details);
  		}
  	},
  }
 }
</script>

<style scoped>
.MyWallet{
	
}
.home-header{
	position: relative;
	display: table;
	height: 150px;
	width: 100%;
	background: white;
}

</style>


